@media (max-width: 450px) {
  .product-container {
    display: grid;
    grid-template-columns: 1fr 1fr;
    padding: 5%;
    grid-gap: 20px;
  }
  .card-img-top {
    height: 260px;
    max-width: 290px;
    object-fit: cover;
    object-position: 0 50%;
    padding: 7%;
    margin: 0 auto;
  }
  [src="https://jaybenaim.github.io/isell/images/img5.jpg"] {
    object-fit: contain;
    object-position: 0 52%;
  }
}

@media (min-width: 751px) {
  .product-container {
    display: grid;
    grid-template-columns: 1fr 1fr 1fr;
    grid-gap: 10px;
    background-color: #f8f8f8;
  }
  .card-img-top {
    height: 258px;
    object-fit: cover;
    object-position: 0 54%;
    margin-top: 5%;
  }
  .related-images {
  }
  .product-container div > h5 {
    margin-top: 10%;
  }
  .product-container > .card {
    width: 20em !important;
    margin-top: 20% !important;
  }
  [src="https://jaybenaim.github.io/isell/images/img5.jpg"] {
    object-fit: contain;
    object-position: 0 52%;
  }
  [src="https://jaybenaim.github.io/isell/images/img8.jpg"] {
    object-fit: cover;
    margin-left: -2px;
  }
  .product-card-3 img,
  .product-card-1 img {
    object-position: 0 52%;
  }
  .product-card-1 {
    grid-area: 1/1;
  }
  .product-card-2 {
    grid-area: 1/2;
  }
  .product-card-3 {
    grid-area: 1/3;
    object-position: 0 54%;
  }

  .product-card-4 {
    grid-area: 2/1;
  }
  .product-container > .products-card {
    width: 14em !important;
    margin: 0 auto;
    border-radius: 20px;
  }

  .product-card-5 {
    grid-area: 2/2;
  }
  .product-card-6 {
    grid-area: 2/3;
  }
  .product-card-7 {
    grid-area: 3/1;
  }
  .product-card-8 {
    grid-area: 3/2;
  }
  .product-card-9 {
    grid-area: 3/3;
  }
  .product-card-10 {
    grid-area: 4/1;
  }
  .product-card-11 {
    grid-area: 4/2;
  }
  .product-card-12 {
    grid-area: 4/3;
  }
}
@media (min-width: 899px) {
  .product-container {
    display: grid;
    grid-template-columns: 1fr 1fr 1fr 1fr;
    grid-template-rows: 1fr 1fr 1fr;
  }
  .product-card-1 {
    grid-area: 1/1;
  }
  .product-card-2 {
    grid-area: 1/2;
  }
  .product-card-3 {
    grid-area: 1/3;
  }
  .product-card-4 {
    grid-area: 1/4;
  }
  .product-card-5 {
    grid-area: 2/1;
  }
}
.qty-input,
.add-to-cart-btn {
  border-radius: 4px;
  margin-right: 4px;
  margin-bottom: 10px;
}
.card-text {
  font-size: 1.4em;
}
.card-text:hover {
  cursor: pointer;
}
